<template>
  <div class="toast" :class="`${visible ? 'show' : 'hide'} ${type ?? 'success'}`">
    <div class="textBox">
      <svg-icon icon-class="success" style="color: rgb(0,180,42)" v-if="type === 'success'" />
      <svg-icon icon-class="error" style="color: rgb(245,63,63)" v-if="type === 'error'" />
      <span class="text">{{ label }}</span>
    </div>
  </div>
</template>
<script>

const ToastTypes = {
  success: 'success',
  error: 'error',
}

export default {
  name: 'toast-component',
  data() {
    return {
      type: ToastTypes.success,
      label: '',
      timeout: 3000, // ms
      visible: false,
    }
  },  
  created() { 
   },
  methods: {
    success(message){
      this.type = ToastTypes.success;
      this.label = message;
      this.visible = true;
      setTimeout(() => {
        this.visible = false;
      }, this.timeout);
    },
    error(message){
      this.type = ToastTypes.error;
      this.label = message;
      this.visible = true;
      setTimeout(() => {
        this.visible = false;
      }, this.timeout);
    }
  },
  components: {
  }
}
</script>

<style lang="less" scoped>
.toast {
  position: fixed;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  pointer-events: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  &.show {
    display: block;
  }

  &.hide {
    display: none;
  }
  .svg-icon{
    margin-right: 8px
  }
  .textBox {
    position: absolute;
    display: flex;
    align-items: center;
    padding: 10px 16px;
    line-height: 1;
    border-radius: 4px;
    border: 1px solid rgb(229, 230, 235);
    margin-bottom: 16px;
    text-align: center;
    box-shadow: 0 4px 10px rgb(0 0 0 / 10%);
    overflow: hidden;
    pointer-events: auto;
    transition: opacity .1s linear;
    background-color: #fff;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);

    .text {
      font-size: 14px;
      color: rgb(29, 33, 41);
    }
  }
}
</style>
