import Vue from "vue";
import Toast from './toast.vue'

// register globally
// Vue.component('Toast', Toast)
const toast=Vue.extend(Toast);

let instance;

const ToastTypes = {
  success: 'success',
  error: 'error',
}
const types = Object.keys(ToastTypes);
Vue.prototype.$toast = function(options){
  if(!types.includes(options.type)){
    throw `type must be in ${types}`;
  }
  if (!instance) {
    //创建div标签并挂载Vue对象
    instance=new toast({
      el:document.createElement('div')
    })
    document.body.appendChild(instance.$el);
  }
  instance[options.type](options.text);
}