  props: {
    payment: String,
    code: String
  },
  data() {
    return {

    }
  },
  methods: {
    copyUPI() {
      copy(this.code);
      this.$toast({type: 'success', text: 'copy success'})
    },
  },
  components: {
    IptBox
  }
};
</script>

