import axios from "axios";
import Vue from "vue";

async function api({ url, data }) {
  const res = await axios(url, {
    method:'post',
    headers: {
      "content-type": "application/json",
    },
    data,
  });

  if (res.data.errNo !== 0) {
    Vue.prototype.$toast({type: 'error', text: res.data.errStr});
  }
  return res.data || {};
}

export function getOrderDetail(orderNo) {
  return api({
    url: "/api/order/detail",
    data: {
      orderNo,
    },
  });
}
export function submitOrder(orderNo, utr) {
  return api({
    url: "/api/order/submit",
    data: {
      orderNo,
      utr,
    },
  });
}
export function payCheck(orderNo, utr) {
  return api({
    url: "/api/order/checkpay",
    data: {
      orderNo,
      utr,
    },
  });
}
