<template>
  <div class="countDown">
    <span class="num">{{ firstNum }}</span>
    <span class="point">:</span>
    <span class="num">{{ secondNum }}</span>
  </div>
</template>

<script>
let timer = null;
export default {
  name: 'count-down',
  props: {
    time: Number,
  },
  data() {
    return {
      countTime: this.time
    }
  },
  created() {
    this.count();
  },
  beforeDestroy() {
    if (timer) {
      clearInterval(timer);
      timer = null
    }
  },
  methods: {
    count() {
      timer = setInterval(() => {
        if (this.countTime <= 0) {
          clearInterval(timer);
          timer = null;
          this.$emit("time-end");
          return;
        }
        this.countTime--;
      }, 1000)
    }
  },
  computed: {
    firstNum(ctx) {
      const min = parseInt(ctx.countTime / 60);
      return min < 10 ? '0' + min : min;
    },
    secondNum(ctx) {
      const second = parseInt(ctx.countTime % 60);
      return second < 10 ? '0' + second : second;
    }
  }
}
</script>

<style lang="less" scoped>
.countDown {
  .num {
    display: inline-block;
    width: 24px;
    background: url('@/assets/num.png') no-repeat;
    background-size: cover;
    font-weight: 600;
    color: #07448D;
    text-align: center;
    height: 28px;
    line-height: 28px;
    font-size: 16px;

  }

  .point {
    display: inline-block;
    height: 28px;
    line-height: 28px;
    font-size: 16px;
    margin: 0 3px;
  }

}
</style>
