<template>
  <BgComponent>
    <div class="result">
      <div class="img" :class="status"></div>
      <div class="title">
        Confirming your payment<br /> result, please wait...
      </div>
      <div class="order">order No. 141f62b6963d71</div>
      <CountDown v-if="status === 'waitting'" :time="300" @time-end="timeEnd"></CountDown>
      <div class="money-box">
        <div class="title">Payment amount</div>
        <div class="money">₹ {{ payment }}</div>
      </div>
      <div class="utrText">My UTR/Re No.</div>
      <IptBox type="password" :msg="utr" disabled @onChange="onChange"></IptBox>
      <SubmitBtn text="Resubmit" @click="resubmit"></SubmitBtn>
    </div>
  </BgComponent>
</template>

<script>
import BgComponent from "@/components/bg.vue";
import IptBox from "@/components/ipt-box.vue";
import SubmitBtn from "@/components/submit-btn.vue";
import CountDown from "@/components/count-down.vue";
import { payCheck, submitOrder } from "@/util/api";

const StatusConf = {
  success: 'success',
  failed: 'failed',
  waitting: 'waitting',
}

const PayStatusMap = {
  0: StatusConf.waitting,
  1: StatusConf.success,
  2: StatusConf.failed,
}

export default {
  name: "result-page",
  data() {
    const { orderNo, utr, payment } = this.$route.query;
    return {
      status: StatusConf.waitting,
      statusImg: StatusConf[this.status],
      payment,
      orderNo,
      utr,
      timer: null,
    }
  },
  created() {
    document.title = 'OrderStatus'
    this.status = StatusConf.waitting;
    this.checkPayStatus();
  },
  methods: {
    async checkPayStatus() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      const { errNo, data } = await payCheck(this.orderNo, this.utr)
      if (errNo === 0) {
        // 0未完成支付，1支付成功，2支付失败
        const { payStatus, setTime } = data;
        this.status = PayStatusMap[payStatus];
        // 如果还是未完成支付需要等待时间轮训请求
        if (this.status === StatusConf.waitting) {
          this.timer = setTimeout(() => {
            this.checkPayStatus();
            this.timer = null;
          }, setTime * 1000);
        }
      }
    },
    timeEnd() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.status = StatusConf.failed;
    },
    onChange(val) {
      this.utr = val;
    },
    async resubmit() {
      const { errNo } = await submitOrder(this.orderNo, this.utr);
      if (errNo === 0) {
        this.status = StatusConf.waitting;
        if (this.utr !== this.$route.query.utr) {
          this.$router.push(`/result?orderNo=${this.orderNo}&utr=${this.utr}&payment=${this.payment}`);
        }
        this.checkPayStatus();
      }
    }
  },
  components: {
    IptBox,
    SubmitBtn,
    CountDown,
    BgComponent,
  },
};
</script>

