<template>
  <BgComponent>
    <div class="payment">
      <DetailCard :order-no="orderNo" :payment="payment" />
      <CopyCard :payment="payment" :code="upi" />
      <SubmitCard :payment="payment" @submit="submit" />
      <!-- <Toast message="成功"></Toast> -->
    </div>
  </BgComponent>
</template>

<script>
import { getOrderDetail, submitOrder } from "@/util/api";
import BgComponent from "@/components/bg.vue";
import DetailCard from "./DetailCard.vue";
import CopyCard from "./CopyCard.vue";
import SubmitCard from "./SubmitCard.vue";

// import Toast from "@/components/Toast/toast.vue";

export default {
  name: "payment-page",
  data() {
    const { orderNo } = this.$route.query;
    return {
      orderNo,
      payment: '0',
      upi: ''
    }
  },
  created() {
    document.title = 'OrderDetail'
    this.getData();
  },
  methods: {
    async getData() {
      const { data } = await getOrderDetail(this.orderNo)
      const { payment, upi } = data;
      this.payment = payment;
      this.upi = upi;
    },
    async submit(utr) {
      if (!utr) {
        this.$toast({type: 'error', text: 'please input utr'});
        return;
      }
      const { errNo } = await submitOrder(this.orderNo, utr);
      if (errNo === 0) {
        this.$router.push(`/result?orderNo=${this.orderNo}&utr=${utr}&payment=${this.payment}`);
      }
    }
  },
  components: {
    DetailCard,
    CopyCard,
    SubmitCard,
    BgComponent,
  },
};
</script>

