<template>
  <div class="iptBox">
    <input class="msg" ref="iptRef" :placeholder="placeholder" :type="curIptType || 'text'" :value="msg"
      @input="iptChange" :disabled="isDisabled" />
    <div v-if="type === IptType.password" class="editBtn" @click="editContent">Edit</div>
  </div>
</template>

<script>
const IptType = {
  text: 'text',
  password: 'password',
}

export default {
  name: 'ipt-box',
  props: {
    msg: String,
    placeholder: String,
    type: String,
    disabled: Boolean,
  },
  data() {
    return {
      IptType,
      isDisabled: !!this.$props.disabled,
      curIptType: this.$props.type,
    }
  },
  methods: {
    editContent() {
      this.isDisabled = !this.isDisabled;
      this.curIptType = this.isDisabled ? 'password' : 'text';
      this.$nextTick(() => {
        this.$refs['iptRef'].focus();
      })
    },
    iptChange(event) {
      this.$emit('onChange', event.target.value);
    }
  },
}
</script>

