<template>
  <div class="submit-card">
    <div class="title">
      <span class="hightlight">Step 2: Transfer</span>
      <span class="commonText">&nbsp;₹ {{ payment }} to the following UPI</span>
    </div>
    <IptBox :msg="curCode" @onChange="iptChange" placeholder="input 12-digit here" />
    <SubmitBtn text="Submit UTR" @click="submit"></SubmitBtn>
  </div>
</template>

<script>
import IptBox from "@/components/ipt-box.vue";
import SubmitBtn from "@/components/submit-btn.vue";

export default {
  name: "submit-card",
  props: {
    payment: String,
  },
  data() {
    return {
      curCode: ''
    }
  },
  methods: {
    submit() {
      this.$emit("submit", this.curCode)
    },
    iptChange(val) {
      this.curCode = val;
    }
  },
  components: {
    IptBox,
    SubmitBtn
  }
};
</script>

